// ЗАКАЖИТЕ БЕСПЛАТНУЮ КОНСУЛЬТАЦИЮ
function submitEmailT(params) {
  let str = window.atob("aHR0cHM6Ly9nZXRzaW1wbGVmb3JtLmNvbS9tZXNzYWdlcz9mb3JtX2FwaV90b2tlbj0yNTE5NDA0MDAwM2RhMzJkNTU3YWZiMjQ1ODUxNzgzZA==");

  fetch(`${str}`, {
    credentials: 'same-origin',
    mode:'no-cors',
    method: 'POST',

    headers: {
      "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"
    },

    body: `company='ТРАНС ВЕКТОР'&kvitancija=${params.kvitancija}&phone=${params.phone}`
  })
  .catch(error => console.log(error))
}

function validaionFormSaleT(e, form) {
  e.preventDefault();

  let kvitancija = form[0].value;
  let phone = form[1].value;

  submitEmailT({ kvitancija: kvitancija, phone: phone});

  form[0].value = "";
  form[1].value = "";

  alert( "Успешно отправлено!" );
}

// modal form
let modalFormSaleT = document.querySelector('#tracking');
modalFormSaleT.addEventListener('submit', e => validaionFormSaleT(e, modalFormSaleT));

